// COLORS
$color-black: #000;
$color-white: #fff;
$color-light-grey: #BDBDBD;
$color-grey: #3C3C43;
$color-light-blue: #007AFF;

// FONTS
$font-font-name: "SFNS Display", sans-serif;

// EASING TRANSITIONS
$duration-normal: 0.4s;
$easing-back-out: cubic-bezier(0.25, 0.74, 0.22, 1.1); // http://cubic-bezier.com/#.25,.74,.22,1.1
$easing-back-in: cubic-bezier(0.47, -0.1, 0.5, -0.06); // http://cubic-bezier.com/#.47,-0.1,.5,-.06
$easing-in-out: ease-in-out;
$easing-out: ease-out;
$easing-in: ease-in;

// URL
$url-font: "../fonts/";
$url-image: "../images/";
