:root {
	--width-base: 1440;
	--width-current: 1600px;
	--width-multiplier: var(--width-current) / var(--width-base);

	@include desktop-aspect {
		--width-base: 800;
		--width-current: 100vh;
	}

	@include desktop-w {
		--width-current: 100vw;

		@include desktop-aspect {
			--width-base: 720;
		}
	}

	@include tablet {
		--width-base: 768;
		--width-current: 100vmin;
	}

	@include mobile {
		--width-base: 375;
		--width-current: 100vmin;
	}
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

html {
	min-height: 100vh;
	box-sizing: border-box;
}

body {
	position: relative;
	font-family: $font-font-name;
	color: $color-white;
	font-weight: 400;
	text-align: center;
	-webkit-font-smoothing: antialiased;
	text-decoration-skip: objects;
	text-rendering: optimizeLegibility;
	text-size-adjust: 100%;
	-webkit-tap-highlight-color: transparent;
	display: flex;
	flex-direction: column;
	height: 100vh;
	background-color: $color-black;
	scroll-behavior: smooth;
	overflow-y: scroll;
}

main {
	display: flex;
	flex-direction: column;
	scroll-behavior: smooth;
}

h1,
h2,
h3,
h4,
p {
	margin: 0;
}

li,
ul {
	margin: 0;
	padding: 0;
}

a,
button {
	outline: none;
	text-decoration: none;
	cursor: pointer;
}

a {
	color: currentColor;
}

button {
	display: block;
	appearance: none;
	border: 0;
	border-radius: 0;
	padding: 0;
	box-shadow: none;
	background: none;
}

img {
	display: block;
	max-width: 100%;
}

svg {
	width: 100%;
	height: 100%;
}

video {
	width: 100%;
	height: 100%;
	font-family: "object-fit:cover";
	object-fit: cover;
}

.select2-selection:focus,
.select2-selection:visited,
.select2-selection:active,
.select2-selection:hover,
a:focus,
a:visited,
a:active,
a:hover,
textarea:focus,
textarea:visited,
textarea:active,
textarea:hover,
input:focus,
input:visited,
input:active,
input:hover,
select:focus,
select:visited,
select:active,
select:hover,
button:focus,
button:visited,
button:active,
button:hover {
	outline: 0;
	outline-style: none;
}
