.carousel {
	$p: &;
	background-color: $color-white;

	.swiper-container {
		//max-width: 960px;
		max-width: 480px;
		height: 400px;
		padding-top: 50px;
		padding-bottom: 50px;
		overflow: hidden;
		margin: 0 auto;


		.swiper-wrapper {

			.swiper-slide {
				background-position: center;
				background-repeat: no-repeat;
				background-size: cover;
				width: 300px;
				height: 300px;
				border-radius: 34px;
				position: relative;
				transition: all ease 1s;
				cursor: pointer;

				.tap-button {
					width: 24px;
					height: 24px;
					background: $color-white;
					backdrop-filter: blur(10px);
					border-radius: 5px;
					z-index: 1;
					position: absolute;
					left: 85%;
					top: 20px;


					.tap-svg{
						opacity: 1;
						position: relative;
						padding: 4px 6px 5px 6px;
						width: 13px;
						height: 16px;
					}
					.tap-link {
						position: absolute;
						opacity: 0;
						white-space: nowrap;
					}
				}

				.model-title {
					position: absolute;
					right: 0;
					left: -33%;
					top: 244px;
					font-style: normal;
					font-weight: 700;
					font-size: 32px;
					line-height: 38px;
					align-items: center;
					letter-spacing: 0.25px;
					text-shadow: 5px 2px 6px #000;
				}

				.video-block {
					width: 326px;
					height: 186px;
					border-radius: 22px;
					margin: 66px auto 0;
					opacity: 0;
					position: relative;
					color: $color-white;

					.video {
						width: 100%;
						height: 100%;
						display: flex;
						justify-content: center;
						align-items: center;
					}

					.video-description {
						position: absolute;
						left: 0;
						bottom: 0;
						width: 100%;
						height: 62px;
						box-shadow: inset 16.2px -16.2px 16.2px rgba(0, 0, 0, 0.1), inset -16.2px 16.2px 16.2px rgba(0, 0, 0, 0.1);
						backdrop-filter: blur(5px);
						border-radius: 0 0 22px 22px;

						font-weight: 700;
						font-size: 12px;
						line-height: 18px;
						text-align: left;
						letter-spacing: 0.35px;

						.video-title, .video-reviews {
							padding-left: 13px;
							opacity: 1;
							font-weight: 700;
							font-size: 12px;
							line-height: 18px;
							letter-spacing: 0.35px;
						}

						.video-title {
							margin-top: 12px;
						}

						.video-reviews {
							color: $color-light-grey;
						}

						.continue-button {
							width: 215px;
							height: 42px;
							margin: 72px auto;
							background: $color-light-blue;
							border-radius: 24px;
							color: $color-white;
							font-weight: 700;
							font-size: 17px;
							line-height: 20px;
							cursor: pointer;
						}
					}
				}

				.link-to-more-video {
					margin: 10px auto 0;
					font-size: 14px;
					line-height: 22px;
					text-decoration: underline;
					letter-spacing: 0.35px;

					color: $color-light-blue;
					opacity: 0;

				}
			}

			.max-size {
				width: 110% !important;
				transform: none!important;
				border-radius: 0;
				left: -22%;
				animation: fadeOut 1s;
				-webkit-animation: fadeOut 1s;
				-moz-animation: fadeOut 1s;
				-o-animation: fadeOut 1s;
				-ms-animation: fadeOut 1s;

				&::before {
					content: "";
					position: fixed;
					top: 0;
					left: 0;
					right: 0;
					z-index: -1;

					display: block;
					background-color: black;
					background-size:cover;
					width: 100%;
					height: 100%;
				}
			}

			@keyframes fadeOut {
				0% { opacity: 0; }
				100% { opacity: 1; }
			}

			@-moz-keyframes fadeOut {
				0% { opacity: 0; }
				100% { opacity: 1; }
			}

			@-webkit-keyframes fadeOut {
				0% { opacity: 0; }
				100% { opacity: 1; }
			}

			@-o-keyframes fadeOut {
				0% { opacity: 0; }
				100% { opacity: 1; }
			}

			@-ms-keyframes fadeIn {
				0% { opacity: 0; }
				100% { opacity: 1; }
			}

			.title-to-top {
				font-size: 24px!important;
				line-height: 28px!important;
				top: 27px!important;
				right: 50%!important;
				left: 0!important;
				letter-spacing: -0.1px!important;
			}

			.tap-to-link {
				background: transparent!important;
				color: $color-light-blue;
				width: 78px!important;
				top: 33px!important;
				left: 66%!important;
				font-size: 14px!important;
				line-height: 22px!important;
			}

			.show-item {
				opacity: 1!important;
			}



		}
	}

	.max-width {
		max-width: 100%!important;
	}

	.hidden {
		opacity: 0!important;
		display: none;
	}

	.show {
		opacity: 1!important;
	}

	.video-border-radius {
		border-radius: 22px;
	}

	.max-height {
		height: 100%!important;
	}


}
