.plans {
	$p: &;
	flex: 1 0 auto;

	&__container {
		max-width: 960px;
		margin: 0 auto;
		display: flex;
		flex-direction: column;

		.plans-header {
			margin: 33px auto 0;
			display: flex;
			justify-content: center;
			align-items: center;
			flex-wrap: wrap;
			width: 280px;

			h1 {
				font-style: normal;
				font-weight: 700;
				font-size: 32px;
				line-height: 38px;
				letter-spacing: 0.35px;
			}
		}

		.plans-description {
			margin: 16px auto 0;
			display: flex;
			justify-content: center;
			align-items: center;
			flex-wrap: wrap;
			min-width: 280px;

			font-style: normal;
			font-weight: 700;
			font-size: 22px;
			line-height: 22px;
			letter-spacing: 1px;
			opacity: .6;
		}

		.plans-block {
			margin: 24px auto 0;
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			min-width: 290px;
			max-width: 290px;

			.active {
				background-color: $color-light-blue!important;
				color: $color-white!important;
				span, a {
					color: $color-white!important;
				}
			}

			.plan {
				background-color: $color-white;
				min-width: 140px;
				height: 200px;
				filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
				color: $color-black;
				border-radius: 10px;
				margin-bottom: 10px;
				cursor: pointer;

				.separator {
					width: 100%;
					height: 1px;
					background-color: $color-light-grey;
					opacity: .5;
				}

				.offer, .duration, .price, .link {
					display: flex;
					justify-content: center;
					align-items: center;
					font-style: normal;
					font-size: 12px;
					line-height: 22px;
					letter-spacing: 0.35px;
					opacity: .75;
				}

				.offer {
					margin: 10px 20px;
				}

				.duration {
					margin: 22px 20px 0;
					text-transform: uppercase;
				}

				.price {
					margin: 16px 0 30px;
					align-items: end;
					color: $color-grey;
					font-size: 13px;
					line-height: 16px;

					.value {
						font-weight: 700;
						letter-spacing: 1.7px;
						font-size: 30px;
						line-height: 28px;
					}
				}

				.link {
					margin-top: 7px;
					font-size: 14px;
					color: $color-light-blue;
				}
			}
		}

	}
}

@media (min-width: 650px) {
	.plans-header{
		width: 100%!important;
	}
	.plans-block {
		max-width: 940px!important;
		min-width: 650px!important;
	}
}