.footer {
	$p: &;
	flex: 0 0 auto;

	&__container {

		max-width: 960px;
		margin: 15px auto 0;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;

		.payment-methods {
			min-width: 290px;
			display: flex;
			flex-direction: column;

			.payment-header {
				display: flex;
				justify-content: center;
				align-items: center;
				font-style: normal;
				font-weight: 700;
				font-size: 14px;
				line-height: 22px;
				letter-spacing: .85px;
				opacity: .6;
			}

			.payment-logos {
				margin-top: 10px;
				display: flex;
				justify-content: space-between;
				align-items: center;

				.visa {
					width: 50px;
					height: 16px;
				}

				.master-card {
					width: 40px;
					height: 32px;
				}

				.discover {
					width: 83px;
					height: 14px;
				}

				.diners {
					width: 40px;
					height: 24px;
				}

				.jcb {
					width: 40px;
					height: 28px;
				}
			}
		}
	}
}
